export default {
  name: "App",
  data() {
    return {
      salary: 3350,
      workTime: 160,
      transport: 0,
      workAge: 0,
      city: 5,
      environment: 5,
      pressure: 5,
      education: 5,
      colleague: 5,
      commute: 5,
      morning: 5
    };
  },
  computed: {
    costPerformanceDesc() {
      if (this.costPerformance <= 0.9) {
        return "";
      } else if (this.costPerformance <= 1.1) {
        return "";
      } else if (this.costPerformance <= 1.3) {
        return "";
      } else if (this.costPerformance <= 1.5) {
        return "";
      } else if (this.costPerformance <= 1.7) {
        return "";
      }
      return "";
    },
    softCompositeIndexDesc() {
      if (this.softCompositeIndex <= 0.8) {
        return "";
      } else if (this.softCompositeIndex <= 1) {
        return "";
      } else if (this.softCompositeIndex <= 1.2) {
        return "";
      } else if (this.softCompositeIndex <= 1.4) {
        return "";
      } else if (this.softCompositeIndex <= 1.6) {
        return "";
      }
      return "";
    },
    costPerformance() {
      return (this.environmentCoefficient * this.colleagueCoefficient ** 0.5 * this.morningCoefficient ** 0.5 / this.cityCoefficient ** 2 / this.pressureCoefficient / this.educationCoefficient / this.workAgeCoefficient * (this.salary / (this.workTime + this.transport * this.workTime / 8 / this.commuteCoefficient) / 23.05) ** 0.5 * (160 / this.workTime) ** 2).toFixed(2);
    },
    softCompositeIndex() {
      return (this.cityCoefficient * this.environmentCoefficient / this.pressureCoefficient ** 2 * this.educationCoefficient * this.colleagueCoefficient * this.commuteCoefficient ** 0.5 * this.morningCoefficient ** 0.7).toFixed(2);
    },
    salaryDesc() {
      if (this.salary <= 3760.25) {
        return "";
      } else if (this.salary <= 10 * 3760.25) {
        return "";
      }
      return "";
    },
    salaryCoefficient() {
      return (this.salary / 3760.25) ** 0.5;
    },
    workTimeDesc() {
      if (this.workTime <= 160) {
        return "8";
      } else if (this.workTime <= 168) {
        return "";
      } else if (this.workTime <= 176) {
        return "";
      } else if (this.workTime <= 200) {
        return "";
      }
      return "";
    },
    workTimeCoefficient() {
      return (this.workTime / 160) ** 2;
    },
    transportDesc() {
      if (this.transport <= 0.6) {
        return "";
      } else if (this.transport <= 1) {
        return "";
      } else if (this.transport <= 2) {
        return "";
      } else if (this.transport <= 4) {
        return "";
      }
      return "";
    },
    workAgeDesc() {
      if (this.workAge <= 1) {
        return "";
      } else if (this.workAge <= 3) {
        return "";
      }
      return "hey";
    },
    workAgeCoefficient() {
      return Math.log10(9 + this.workAge);
    },
    cityDesc() {
      if (this.city <= 5) {
        return "";
      } else if (this.city <= 7) {
        return "";
      } else if (this.city <= 9) {
        return "9";
      }
      return "";
    },
    cityCoefficient() {
      return Math.log10(5 + this.city);
    },
    environmentDesc() {
      if (this.environment <= 3) {
        return "";
      } else if (this.environment <= 5) {
        return "";
      } else if (this.environment <= 7) {
        return "";
      } else if (this.environment <= 9) {
        return "";
      }
      return "CBD";
    },
    environmentCoefficient() {
      return this.environment / 20 + 0.75;
    },
    pressureDesc() {
      if (this.pressure <= 3) {
        return "";
      } else if (this.pressure <= 5) {
        return "";
      } else if (this.pressure <= 7) {
        return "";
      } else if (this.pressure <= 9) {
        return "";
      }
      return "";
    },
    pressureCoefficient() {
      return this.pressure / 20 + 0.75;
    },
    educationDesc() {
      if (this.education <= 3) {
        return "";
      } else if (this.education <= 4) {
        return "";
      } else if (this.education <= 6) {
        return "211Top200";
      } else if (this.education <= 7) {
        return "985Top100";
      } else if (this.education <= 9) {
        return "985Top100";
      }
      return "";
    },
    educationCoefficient() {
      return this.education / 20 + 0.75;
    },
    colleagueDesc() {
      if (this.colleague <= 3) {
        return "";
      } else if (this.colleague <= 5) {
        return "";
      } else if (this.colleague <= 7) {
        return "";
      } else if (this.colleague <= 9) {
        return "";
      }
      return "";
    },
    colleagueCoefficient() {
      return this.colleague / 20 + 0.75;
    },
    commuteDesc() {
      if (this.commute <= 3) {
        return "";
      } else if (this.commute <= 5) {
        return "";
      } else if (this.commute <= 7) {
        return "";
      } else if (this.commute <= 9) {
        return "";
      }
      return "";
    },
    commuteCoefficient() {
      return this.commute / 20 + 0.75;
    },
    morningDesc() {
      if (this.morning <= 3) {
        return "";
      } else if (this.morning <= 5) {
        return "";
      } else if (this.morning <= 7) {
        return "";
      } else if (this.morning <= 9) {
        return "";
      }
      return "";
    },
    morningCoefficient() {
      return this.morning / 20 + 0.75;
    }
  }
};